import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { TextField, Button, Divider, Snackbar, IconButton } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import SaveIcon from '@material-ui/icons/Save';
import { Link } from 'gatsby';
import AssignmentIcon from '@material-ui/icons/Assignment';

import { useMsal, useAccount } from "@azure/msal-react";
import { InteractionStatus} from "@azure/msal-browser";
import { loginRequest } from "../configs/AuthConfig";
import { callMsGraph } from "../services/GraphService";
import {BlogCreate, GetAccessToken} from "../services/BlogService";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '50ch',
        },
    },
    divider: {
        margin: theme.spacing(2, 0)
    }
}));

export default function NewBlog() {
    const classes = useStyles();
    const { instance, accounts, inProgress } = useMsal();
    const account = useAccount(accounts[0] || {});
    const [blogId, setBlogId] = React.useState('');
    const [blogTitle, setBlogTitle] = React.useState('');
    const [blogContent, setBlogContent] = React.useState('');
    const [blogExtendedContent, setBlogExtendedContent] = React.useState('');
    const [blogImageUrl, setBlogImageUrl] = React.useState('');

    const postTitleChange = (event) => {
        setBlogTitle(event.target.value);
    };

    const blogContentChange = (event) => {
        setBlogContent(event.target.value);
    };

    const blogExtendedContentChange = (event) => {
        setBlogExtendedContent(event.target.value);
    };

    const blogImageUrlChange = (event) => {
        setBlogImageUrl(event.target.value);
    };

    const handleFileSelect = (event) => {
        let imageFile = event.target.files[0];
        let reader = new FileReader();
        reader.readAsDataURL(imageFile);
        reader.onload = readSuccess;                                            
        function readSuccess(evt) {                        
            console.log(evt.target.result);                                
        };
        setFilePath(imageFile.name);
    };

    const [openSuccessAlert, setOpenSuccessAlert] = React.useState(false);
    const [openFailureAlert, setOpenFailureAlert] = React.useState(false);
    const [filePath, setFilePath] = React.useState('Select Image');

    const handleSubmitClick = () => {
        const newBlog = {
            author: null,
            title: blogTitle,
            imageUrl: blogImageUrl,
            content: blogContent,
            extendedContent: blogExtendedContent
        };
        if (account) {
            instance.acquireTokenPopup({
                // ...loginRequest,
                account: account,
                scopes: ["User.Read"]
            }).then((response) => {
                newBlog.author = account.name;
                console.log(newBlog);                
                console.log(response);
                //GetAccessToken(response.accessToken, newBlog).then(response => console.log(response));
                BlogCreate(response.accessToken, newBlog).then(response => {
                    if(response > 0){
                        setBlogId(response);
                        setOpenSuccessAlert(true);
                    }
                    else{
                        setOpenFailureAlert(true);
                    }
                });
            });
        }
        
    };

    const handleSuccessAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenSuccessAlert(false);
    };

    const handleFailureAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenFailureAlert(false);
    };

    return (
        <form className={classes.root} noValidate autoComplete="off">
            <div>
                <TextField id="standard-basic"
                        label="Post Title"                        
                        value={blogTitle}
                        onChange={postTitleChange}
                />
                <br/><br/>
                <TextField id="standard-basic"
                    label="Post Content"
                    multiline
                    rowsMax={4}
                    value={blogContent}
                    onChange={blogContentChange}
                />
                <br /><br />
                <TextField id="standard-basic"
                    label="Extended Content"
                    multiline
                    rowsMax={4}
                    value={blogExtendedContent}
                    onChange={blogExtendedContentChange}
                />
                <br /><br />
                <TextField id="standard-basic"
                    label="Image Url"
                    value={blogImageUrl}
                    onChange={blogImageUrlChange}
                />
                <br /><br />
                <input accept="image/*" id="icon-button-file" type="file" hidden onChange={handleFileSelect} />
                <label htmlFor="icon-button-file">
                    <IconButton color="primary" aria-label="upload picture" component="span">
                        <PhotoCamera />
                    </IconButton>&nbsp;
                    {filePath}
                </label>
            </div>
            <Divider className={classes.divider} />            
            { blogId > 0 ?
                (<Link to={"/blog#"+ blogId }><Button size="small" startIcon={<AssignmentIcon />} variant="contained" color="primary">Open Blog</Button></Link>)
                : (<Button variant="contained" size="small" startIcon={<SaveIcon />} color="primary" onClick={handleSubmitClick}>Submit</Button>)
            }
            <Snackbar open={openSuccessAlert} autoHideDuration={6000} onClose={handleSuccessAlertClose}>
                <Alert onClose={handleSuccessAlertClose} severity="success">
                    Blog created successfully!
                </Alert>
            </Snackbar>
            <Snackbar open={openFailureAlert} autoHideDuration={6000} onClose={handleFailureAlertClose}>
                <Alert onClose={handleFailureAlertClose} severity="error">
                    Failed to create blog!
                </Alert>
            </Snackbar>
        </form>
    );
}
