import { BlogAPIEndpoint } from "../configs/APIConfig";

export async function GetAccessToken(accessToken) {
    const body = { 'access_token': accessToken };
  
    return fetch(BlogAPIEndpoint.LoginAAD, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body)
      },
    ).then(response => {
      console.log(response);
    });
  }

export async function BlogCreate(accessToken, data) {
    const headers = new Headers();
    const bearer = `Bearer ${accessToken}`;

    headers.append("Authorization", bearer);
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Headers", "Origin, X-Requested-With, Content-Type, Accept");
    headers.append("Content-Type", "application/json");
    
    const requestOptions = {
        method: "POST",
        headers: headers,
        body: JSON.stringify(data)
    };

    return fetch(BlogAPIEndpoint.Create, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}

export async function BlogDelete(accessToken, blogId) {
    
    const requestOptions = {
        method: 'DELETE',
        headers: { 
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + accessToken
        }
    };

    return fetch(BlogAPIEndpoint.Create + '/' + blogId, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}

export async function BlogGetById(blogId) {
    
    const requestOptions = {
        method: 'GET',
        headers: { 
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept",
            'Content-Type': 'application/json'
        }
    };

    return fetch(BlogAPIEndpoint.GetById + '/' + blogId, requestOptions)
        .then(response => response.json())
        .catch(error => console.log(error));
}